@import url(https://fonts.googleapis.com/css2?family=Saira+Condensed:wght@900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400&display=swap);
body {
    margin: 0;
    padding: 0;
    color: #202839;
    background: #c3375e;
}

a {
    text-decoration: none;
}

.App {
    padding: 64px;
}

.Caption {
    margin: 0;
    padding: 0;
    color: #202839;
    font-family: 'Helvetica Neue Condensed Black', 'Saira Condensed', Helvetica;
    font-size: 64px;
    line-height: 64px;
    text-transform: uppercase;
}

.Links {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: baseline;
            align-items: baseline;
}

.Links-Group-Header {
    margin: 0;
    padding: 0;
    color: #202839;
    font-family: 'Helvetica Neue', 'Roboto', Helvetica;
    font-size: 28px;
    line-height: 42px;
    font-weight: 400;
    margin-top: 64px;
}

.Links-Group-Content {
    margin: 0;
    padding: 0;
}

@media only screen
       and (max-width : 640px) {

    .App {
        padding: 32px;
    }

    .Caption {
        font-size: 48px;
        line-height: 48px;
    }

    .Links-Group-Header {
        font-size: 20px;
        line-height: 30px;
        margin-top: 32px;
    }
}


a {
    display: -webkit-flex;
    display: flex;
    margin: 0;
    padding: 0;
    transition: 0.64s;
}

a:hover {
    transition: 0.32s;
    margin-left: -16px;
}

.Link {
    margin: 0;
    padding: 0;
    display: -webkit-flex;
    display: flex;
    margin-top: 24px;
}

.Link-Image {
    margin: 0;
    padding: 0;
    width: 64px;
    height: 64px;
    margin-right: 12px;
}

.Link-Content {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin: 0;
    padding: 0;
    padding-top: 2px;
    padding-bottom: 2px;
}

.Link-Content-Caption {
    margin: 0;
    padding: 0;
    color: #202839;
    font-family: 'Helvetica Neue', 'Roboto', Helvetica;
    font-size: 28px;
    line-height: 30px;
    font-weight: 400;
    text-decoration: none;
}

.Link-Content-Description {
    margin: 0;
    padding: 0;
    color: #202839;
    font-family: 'Helvetica Neue', 'Roboto', Helvetica;
    font-size: 24px;
    line-height: 26px;
    font-weight: 200;
    text-decoration: none;
}

@media only screen
       and (max-width : 640px) {

    .Link {
        margin-top: 16px;
    }

    .Link-Image {
        width: 48px;
        height: 48px;
        margin-right: 8px;
    }

    .Link-Content-Caption {
        font-size: 20px;
        line-height: 24px;
    }

    .Link-Content-Description {
        font-size: 16px;
        line-height: 14px;
    }
}

